#container {
    width: 400px;
    background-color: aliceblue;
    padding: 20px;
    border-radius: 10px;
}

input {
    width: 100%;
    padding: 5px;
    box-sizing: border-box;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
}

ul,li {
    margin: 0;
    padding: 0;
    list-style-type: none;
}

.list {
    margin: 15px 0;
}

.list > li {
    background-color: antiquewhite;
    padding: 5px 10px;
    margin-bottom: 2px;
    display: flex;
    justify-content: space-between;
}

.btn {
    display: flex;
    justify-content:flex-end;
    padding: 5px 0;
}

.btn > input {
    padding: 5px 20px;
    width: auto;
}

#container > h1 {
    text-align: center;
    color: peru;
    margin-bottom: 20px;
}

.totalContact {
    text-align: end;
}